.charts-css {
  &.legend {
    margin: 0;
    padding: 1rem;
    border: 1px solid var( --legend-border-color );
    list-style: none;

    font-size: 1rem;

    li {
      line-height: 2;
      display: flex;
      align-items: center;

      &::before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-inline-end: .5rem;
        border-width: 2px;
        border-style: solid;
      }

      @for $i from 1 through $max-items {

        &:nth-child(#{ $i })::before {
          background-color: var( --color-#{ $i }, transparent );
          border-color: var( --border-color-#{ $i }, var( --border-color, #000 ) );
        }

      }
    }
  }

  // Legend Alignment
  &:not(.legend-inline) {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  // Horizontal Legend
  &.legend-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    li {
      margin-inline-end: 1rem;
    }
  }

  // Circle Label
  &.legend-circle {
    li::before {
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
    }
  }

  // Square Label
  &.legend-square {
    li::before {
      width: 1rem;
      height: 1rem;
      border-radius: 3px;
    }
  }

  // Rhombus Label
  &.legend-rhombus {
    li::before {
      width: 1rem;
      height: 1rem;
      border-radius: 3px;
      transform: rotate(45deg) scale(0.85);
    }
  }

  // Rectangle Label
  &.legend-rectangle {
    li::before {
      width: 2rem;
      height: 1rem;
      border-radius: 3px;
    }
  }

  // Line Label
  &.legend-line {
    li::before {
      width: 2rem;
      height: 3px;
      border-radius: 2px;
    }
  }

}
